$theme-color: $green;
$font-size: 13px;
$btn-link-disabled-color: #ccc;
$input-border: #ccc;
$padding-large-horizontal: 20px;
$border-radius-small: 2px;

.custom-radio,
.custom-checkbox {
    padding-left: $padding-large-horizontal;
    position: relative;
    display: block;

    input {
        opacity: 0;
        position: absolute;
        z-index: 1;
        cursor: pointer;
        margin-left: -$padding-large-horizontal;

        &:checked {
            +label {
                &::before {
                    border-color: $theme-color;
                }

                &::after {
                    content: '';
                    display: inline-block;
                    position: absolute;
                    width: 20px;
                    height: 20px;
                    left: 2px;
                    top: 4px;
                    margin-left: -$padding-large-horizontal;
                    border: 1px solid $theme-color;
                    border-radius: 50%;
                    background-color: $theme-color;
                }
            }
        }
    }

    label {
        display: inline-block;
        position: relative;
        padding-left: 15px;
        font-weight: 300;
        cursor: pointer;
        transition: all 0.3s ease;

        &:hover {
            opacity: 0.8;
            transition: all 0.3s ease;
        }

        &::before {
            content: '';
            display: inline-block;
            position: absolute;
            width: 20px;
            height: 20px;
            left: 0;
            top: 3px;
            margin-left: -$padding-large-horizontal;
            border: 1px solid $input-border;
            border-radius: 50%;
            background-color: #fff;
        }
    }

    &.disabled {
        label {
            color: $btn-link-disabled-color;

            &::before {
                opacity: 0.5;
                border-color: $btn-link-disabled-color;
            }
        }
    }
}

.custom-checkbox {
    input {
        &:checked {
            +label {
                &::before {
                    border-color: $theme-color;
                    background-color: $theme-color;
                }

                &::after {
                    content: "\f00c";
                    font-family: FontAwesome;
                    font-size: $font-size;
                    color: #fff;
                    top: 3px;
                    left: 3px;
                    border-color: transparent;
                    background-color: transparent;
                }
            }
        }
    }

    label {
        &::before {
            border-radius: $border-radius-small;
        }
    }
}

.fm-custom-select {
    position: relative;
    background-color: #fff;
    border: 1px solid #ECECEC;
    height: 40px;
    display: flex;
    border-radius: 3px;
    z-index: 10;

    &:before {
        //font-family: "freshmail-ico";
        display: block;
        content: $freshmail-ico-arrow-right;
        @extend .freshmail-ico;
        position: absolute;
        transform: rotate(90deg);
        right: 15px;
        top: 50%;
        margin-top: -6px;
        transition: all 0.3s ease;
    }

    &--open {
        &:before {
            transform: rotate(-90deg);
            transition: all 0.3s ease;
        }
    }

    &__value {
        display: flex;
        justify-content: flex-start;
        align-content: center;
        align-items: center;
        flex-grow: 1;
        padding: 10px;
        font-size: 15px;

        .fm-custom-select--open & {
            &:before {
                content: "";
                position: fixed;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background-color: transparent;
            }
        }
    }

    &__dropdown {
        position: absolute;
        top: 100%;
        background-color: #FFF;
        border-top: solid 1px #CCC;
        left: -1px;
        width: calc(100% + 1px);
        box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.1);
        border: solid 1px #ECECEC;

        >ul {
            list-style: none;
            margin: 0;
            padding: 0;
            max-height: 300px;
            overflow: auto;
        }
    }

    &__listitem {
        border-bottom: solid 1px #ECECEC;
        padding: 5px 15px;
        transition: all 0.3s ease;

        &:hover {
            background-color: #ECECEC;
            transition: all 0.3s ease;
            cursor: pointer;
        }

        &:last-child {
            border: 0;
        }
    }
}

.input-group{
    &__testmail{
        

        .input-group-addon{
            padding: 0;
            margin: 0;

            .btn{
                background-color: #2eacce;
                border: solid 1px #2eacce;
                color: #FFF;
                margin: 0;
                padding: 4px 20px;
                transition: all 0.3s ease;
                margin-left: 1px;
                position: relative;
                height: 100%;
                min-height: 29px;
                min-width: 150px;

                &:disabled{
                    opacity: 1;
                    background-color: #CCC;
                    border: solid 1px #FFF;
                }

                .freshmail__preloader{

                    .preloader_spinner{
                        transform: scale(0.5);
                        margin-top: -2px;
                    }
                }

                &:hover{
                    background-color: darken(#2eacce, 15%);
                    transition: all 0.3s ease;
                }
            }
        }
    }
}