@import '~quill/dist/quill.core.css';
@import '~quill/dist/quill.snow.css';

$color-black: #000000;
$color-white: #ffffff;
$color-grey: #dddddd;

.m-0 {
  margin: 0 !important;
}

.freshmail__designer {
  display: flex;
  flex-flow: column;

  &--panel {
    padding: 10px !important;
    margin: 0 !important;
  }


  .panel-heading {
    width: 100%;
  }

  .fm-panel-body {
    display: flex;
    padding: 0px;
    width: 100%;
  }


  .viewer {
    width: 70%;
    display: flex;
    justify-content: center;
    border-radius: 5px;
    overflow: hidden;

    .email-body {
      width: 100%;

      .ql-align-left {
        text-align: left;
      }

      .ql-align-center {
        text-align: center;
      }

      .ql-align-right {
        text-align: right;
      }

      .ql-align-justify {
        text-align: justify;
      }

      >div {
        margin: auto auto;
      }
    }

    .table tbody>tr>td {
      border: 0;
    }
  }

  .editor {
    width: 30%;
    background-color: #FFF;
    padding: 0px 0px 15px 15px;
    box-sizing: border-box;

    .btn-group {
      text-transform: uppercase;
    }
  }

  .btn-cta p {
    margin: 0;
    padding: 0;
  }
}

.vc-chrome {
  position: absolute;
}

.designer__block--active {
  position: relative;

  &:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 999;
    box-shadow: inset #e50b70 0px 0px 0px 3px;
  }
}

.form-group__logo {
  display: flex;
  justify-content: space-between;

  .btn {
    text-transform: uppercase;
  }
}

.color-picker {
  width: 100%;
  position: relative;
  display: flex;
  padding: 5px;

  &__value {
    width: 20px;
    height: 20px;
    display: block;
    margin-right: 5px;
    border: solid 1px #000;
  }

  .dropdown-menu {
    padding: 0;
  }

  .dropdown-toggle {
    display: inline-flex;
    padding-left: 0;
    background-color: #FFF;
  }

  .vc-chrome {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 999;
  }
}

#editor_accordion {
  .designer__blockedit {
    .panel.panel-default {
      padding: 16px 0px 0px 0px;
      margin-bottom: 10px;

      .panel-title {
        width: 100%;

        a {
          display: block;
          width: 100%;
        }

        &--invisible {
          opacity: 0.5;
        }
      }

      .block-visibility {
        position: absolute;
        right: 10px;
        top: 5px;
      }

      .panel-heading {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin: -16px 0px 0px 0px;
        border-radius: 3px 3px 0px 0px;
      }

      .panel-body {
        border-top: none;
        padding: 5px 0px 5px 0px;

        hr {
          margin: 5px 0;
        }
      }
    }
  }
}

.template {
  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 15px;
  }

  &__external-nfo {
    .alert {
      margin-bottom: 0 !important;
    }
  }

  &__preview {
    padding: 10px !important;
    margin-bottom: 5px !important;

    .panel-body {
      padding: 15px 0 0 0;
      text-align: center;
    }

    &--selected {
      .template__thumb {
        &:after {
          opacity: 1 !important;
        }
      }

    }

    &--addnew {
      .template__thumb {
        background-size: contain;
      }

      .img_fm {
        max-width: 100px;
      }
    }
  }

  &__new {
    z-index: 2;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;

    a {
      text-decoration: none !important;
    }

    img {
      margin-bottom: 20px;
    }
  }

  &__thumb {
    border-radius: 5px;
    display: flex;
    overflow: hidden;
    background-position: center center;
    background-size: cover;
    width: 100%;
    justify-content: center;
    align-items: center;
    background-color: #f1f4f7;
    position: relative;

    .btn {
      position: absolute
    }

    &:before {
      content: '';
      padding-top: 100%;
    }

    &:after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background-color: rgba(#000, 0.25);
      z-index: 1;
      opacity: 0;
    }

    &:hover {
      &:after {
        opacity: 1 !important;
        transition: all 0.3s ease;
      }

      .template__actions {
        opacity: 1 !important;
      }
    }

    .template__preview--selected & {
      .template__actions {
        opacity: 1 !important;

        i {
          color: green;
        }
      }
    }
  }

  &__actions {
    z-index: 2;
    opacity: 0;
    transition: opacity 0.3s ease;

    &:hover {
      transition: opacity 0.3s ease;
    }

    a,
    i {
      text-decoration: none !important;
    }
  }

  &__btn {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2.5rem;
    border-radius: 1.65rem;
    background-color: #fff;
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    text-align: center;
    color: #363a41 !important;
    padding: 5px 15px;
    z-index: 2;
    border: 0;
    width: 100%;

    &:hover:not([disabled]) {
      background-color: #19a9c6;
      color: #FFF !important;

      i {
        color: #FFF;
      }
    }


    +.template__btn {
      margin-top: 10px;
    }

    &:focus {
      outline: 0;
    }

    i {
      color: #00aff0;
      margin-right: 5px;
    }
  }


}

.slide-fade-enter-active {
  transition: all .3s ease;
}

.slide-fade-leave-active {
  transition: all .3s ease;
}

.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateY(10px);
  opacity: 0;
  height: 0;
  overflow: hidden;
}