// Variables

.freshmail{
  &__preloader{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 100;
    background-color: rgba(255,255,255,0.8);
    

    .preloader_spinner{
      display: flex;
      min-height: 100%;
      align-items: center;
      justify-content: center;
      
      @keyframes spin {
          0% {
              transform: rotate(0deg);
          }
          100% {
              transform: rotate(360deg);
          }
      }

      svg{
        width: 32px;
        height: 32px;
        animation-name: spin;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
        animation-duration: 0.3s;
      }
    }
  }
}