// Variables

.freshmail {
    &__wizard {
        overflow: hidden;
        position: relative;
        background-color: $white;
        font-family: 'Ubuntu', sans-serif;

        section {
            padding: 30px;
            display: flex;
            flex-flow: column;
        }

        .benefits__box {
            padding: 15px 60px;

            @media(max-width:768px) {
                padding: 0px;
            }

            h5 {
                font-size: 28px;
                display: flex;
                min-height: 90px;
                padding-right: 40%;

                @media(max-width:768px) {
                    min-height: 0;
                    justify-content: center;

                    br {
                        display: none;
                    }
                }
            }

            p {
                line-height: 2em;

                @media(max-width:768px) {
                    text-align: justify;
                }
            }

            .benefits__image {
                @media(max-width:768px) {
                    text-align: center;
                    margin: 30px auto;
                }
            }

        }

        .section-bg {
            &--green {
                background-color: $green;
            }

            &--white {
                background-color: $white;
            }

            &--grey {
                background-color: $gray-100;
            }

            &--nopadding {
                padding: 0;
            }


        }

        .d-flex {
            display: flex;
        }

        .justify-content-between {
            justify-content: space-between;
        }

        .ml-auto {
            margin-left: auto;
        }

        .header__logo {
            margin-top: 56px;
            margin-bottom: 0;

            img {
                max-height: 40px;
            }

        }
    }

    .wizard__alert{
        background-color: #f7f3d0;
        border-color: #f2e0b1;
        color: #31393a;
        width: 90%;
        margin: auto auto;
        padding: 25px 45px 25px 45px;
        position: relative;

        &:before{
                @extend %freshmail-ico;
                content: $freshmail-ico-info;
                position: absolute;
                top: 50%;
                margin-left: 7px;
                left: 5px;
                font-size: 22px;
                color: #99925c;
                transform: translateY(-50%);
        }
    }



    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        font-family: 'Dosis', sans-serif;
    }

    h2 {
        font-size: 50px;
        font-weight: 100;
    }

    h4 {
        font-size: 36px;
        font-weight: 500;
        letter-spacing: 0.5px;
    }

    .text-white {
        color: $white;
    }

    .form-group {
        position: relative;

        &--token {

            input {
                border: 0;
                font-size: 16px;
                border-radius: 5px;
                height: auto;
                padding: 10px 10px 10px 40px;
                background-color: $white !important;
            }

            &:after {
                @extend %freshmail-ico;
                content: $freshmail-ico-key;
                position: absolute;
                top: calc(50% - 10px);
                font-size: 25px;
                margin-left: 7px;
                left: 5px;
                font-size: 18px;
                color: #cccbcb;
            }
        }
    }

    .configuration__summary {
        margin: 0;
        padding: 0;

        li {
            display: flex;
            margin-bottom: 20px;
            font-size: 1.3em;
            justify-content: flex-start;
            align-items: center;

            i {
                margin-right: 10px;
            }
        }
    }

    .synchronize__notification {
        background-color: #000;
        padding: 40px;
        text-align: center;
        color: #FFF;
        border-radius: 3px;
        position: relative;
        margin: 25px auto 0 auto;
        max-width: 70%;
        transition: all 0.5s ease-in;
        animation-duration: 1s;
        animation-delay: 0.5s;
        animation-name: fadeInDown;
        animation-fill-mode: forwards;
        opacity: 0;


        @keyframes fadeInDown {
            from {
                opacity: 0;
                transform: translateY(50px)
            }

            to {
                opacity: 1;
                transform: translateY(0px)
            }
        }

        p {
            font-size: 1.3em;
            font-weight: 300;
        }

        &:after {
            bottom: 100%;
            left: 50%;
            border: solid transparent;
            content: "";
            height: 0;
            width: 0;
            position: absolute;
            pointer-events: none;
            border-color: rgba(0, 0, 0, 0);
            border-bottom-color: #000000;
            border-width: 30px;
            margin-left: -30px;
        }
    }

    .freshmail-ico-circle {
        font-size: 20px;
        background-color: #dadae0;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $white;

        &--green {
            background-color: $green;
        }
    }

    .footer__buttons {
        margin: auto;
        width: 100%;
        max-width: 300px;
        text-align: center;
        display: flex;
        margin: 50px auto;
        justify-content: space-between;
    }

    .row--center {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}



.wizard {


    &__header {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
    }

    &__progress {
        display: flex;
        margin-right: 30px;
    }

    &__step {
        transition: all 0.3s ease-in;
    }

    &-step {
        width: 38px;
        height: 38px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0;
        border-radius: 50%;
        margin: 5px;
        font-family: 'Dosis', sans-serif;
        font-size: 1.6em;
        font-weight: 600;
        background-color: $gray-100;
        color: $gray-300;
        transition: all 0.3s ease-in;
        //cursor: pointer;

        &--done {
            background-color: $gray-100;
            color: $gray-300;
            transition: all 0.3s ease-in;
        }

        &--current {
            background-color: $green;
            color: $white;
            transition: all 0.3s ease-in;
        }

        &--hidden {
            display: none;
        }
    }

    &__badge {
        width: 100%;
        text-align: center;
        padding: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $gray-100;
        font-size: 12px;
        margin-top: 60px;
        font-weight: 100;

        img {
            margin-right: 10px;
        }
    }

    &__benefits {
        margin: auto auto;
        width: 100%;
        max-width: 1200px;



        .benefits {

            &__header {
                margin-bottom: 30px;
                display: flex;
                border-bottom: solid 1px #CCC;
                padding-bottom: 30px;
                margin-top: 74px;

                >div {
                    width: 33%;
                    display: flex;
                    font-size: 2em;
                    color: $black;
                    font-family: 'Dosis', sans-serif;
                    justify-content: center;

                    @media (max-width: 600px) {
                        font-size: 11px;
                        padding: 15px;
                        box-sizing: border-box;
                        text-align: center;
                    }

                    &:first-child {
                        justify-content: flex-start;
                    }
                }
            }

            &__row {
                display: flex;
                padding: 10px 0px;

                &--title {
                    justify-content: flex-start !important;
                    font-size: 1.2em;
                }

                &-item {
                    width: 33%;
                    line-height: 15px;
                    min-height: 30px;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    &--title {
                        font-size: 1.4em;
                    }
                }
            }
        }
    }


}

$spaceamounts: (0, 5, 10, 15, 20, 25, 30, 35, 40, 45, 50, 75, 100, 110);
$sides: (top, bottom);

@each $space in $spaceamounts {
    @each $side in $sides {
        .m#{str-slice($side, 0, 1)}-#{$space} {
            margin-#{$side}: #{$space}px !important;
        }

        .p#{str-slice($side, 0, 1)}-#{$space} {
            padding-#{$side}: #{$space}px !important;
        }
    }
}