// Variables

.freshmail {
    .btn {
        transition: all 0.3s ease;
        border: 0;
        border-radius: 2em;
        background-color: $white;
        text-decoration: none;
        font-family: 'Dosis', sans-serif;
        line-height: 1.2em;
        letter-spacing: 0.01em;
        padding: 0.6em 1.8em;
        font-size: 1.2em;
        outline: none !important;
        max-height: 36px;
        box-shadow: none !important;


        &--yellow {
            background-color: $yellow;
            border: solid 1px $yellow;
            color: $black;

            &:hover {
                background-color: darken($yellow, 15%);
                border-color: darken($yellow, 15%);
                ;
            }
        }

        &--white {
            background-color: $white;
            border: solid 1px $gray-200;
            color: $black;

            &:hover {
                background-color: darken($white, 15%);
                border-color: darken($white, 15%);
            }
        }

        &--round {
            width: 30px;
            height: 30px;
            display: flex;
            border-radius: 50%;
        }

        &--arrow {
            padding: 0.6em 1.2em 0.6em 1.8em;

            &:after {
                @extend %freshmail-ico;
                content: $freshmail-ico-arrow-right;
                position: relative;
                top: calc(50% - 5px);
                font-size: 12px;
                margin-left: 5px;

            }

            &-left {
                padding: 0.6em 1.8em 0.6em 1.2em;

                &:after {
                    display: none !important;
                }

                &:before {
                    @extend %freshmail-ico;
                    content: $freshmail-ico-arrow-left !important;
                    position: relative;
                    top: calc(50% - 5px);
                    font-size: 12px;
                    margin-left: 5px;

                    @-moz-document url-prefix() {
                        top: auto;
                    }
                }
            }

            &.btn--white {

                &:after {
                    color: $green;
                }

            }
        }

        &--link {
            text-decoration: underline;
            color: $gray-800;
            cursor: pointer;

            &:hover {
                color: lighten($gray-800, 15%);
                transition: all 0.3s ease-in;
                text-decoration: underline;
            }
        }
    }
}