.fade-leave-active {
  transition: 0.5s;
  opacity: 0;
}

.fade-enter-active {
  transition: 0.5s;
  opacity: 1;
}

.fade-enter {
  opacity: 1;
}

.fade-leave-to {
  opacity: 0;
}

.component-fade-enter-active,
.component-fade-leave-active {
  transition: opacity .3s ease;
}

.component-fade-enter,
.component-fade-leave-to

/* .component-fade-leave-active below version 2.1.8 */
  {
  opacity: 0;
}